nav{
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: .8rem 1.8rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    font-size: 1.5rem;
    display: flex;
    gap: 1rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}
nav a{
    background: transparent;
    padding: .6rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.4rem;
}

nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}
@media screen and (max-width:600px) {
    nav{
        padding: .5rem .5rem;
    }
}

@media screen and (max-width:350px) {
    nav a{
        padding: .3rem .3rem;
        font-size: 1rem;
       
    }
  
  
}